*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: "Noto Sans TC", "Roboto", sans-serif;
  line-height: 1.5;
}

img {
  object-fit: cover;
  vertical-align: middle;
  max-width: 100%;
  height: auto;
}

a {
  color: inherit;
}

p,
li {
  font-size: 20px;
}
